import { elementScrollIntoView } from "seamless-scroll-polyfill";

const blogSection = () => {

  const scrolling = () => {

    const link1 = document.getElementById('link1')
    const link2 = document.getElementById('link2')
    const link3 = document.getElementById('link3')
    const link4 = document.getElementById('link4')
    const link5 = document.getElementById('link5')
    const resumo = document.getElementById('resumoLink')
    const voltar = document.getElementById('voltar')
    const voltar2 = document.getElementById('voltar2')

    const link1_2 = document.getElementById('link1_2')
    const link2_2 = document.getElementById('link2_2')
    const link3_2 = document.getElementById('link3_2')
    const link4_2 = document.getElementById('link4_2')
    const resumo2 = document.getElementById('resumoLink_2')

    
    const allLinks = []
    allLinks.push(link1, link2, link3, link4, link5, resumo, voltar, voltar2, link1_2, link2_2, link3_2, link4_2, resumo2)

    
    allLinks.forEach((link) => {
      link.addEventListener('click', (event) => {
        event.preventDefault()
        const id = link.getAttribute('href').substring(1)
        
        const target = document.getElementById(id)
        console.log(target)
        elementScrollIntoView(target, { behavior: "smooth", block: "start" , inline: "center" })
      }) 
    })    
  }
  scrolling()
}

export default blogSection